\r\n  \r\n  // Fonts\r\n  $font-family: 'Poppins', Arial, sans-serif;\r\n  \r\n  *[tabindex=\"0\"]:focus {\r\n    outline: 4px dashed orange;\r\n  }\r\n\r\n  body{\r\n    font-family: $font-family;\r\n  }\r\n\r\n  // Header\r\n  header {\r\n    background-color: $secondary-color;\r\n    color: #fff;\r\n    padding: 20px 50px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    position: fixed;\r\n    width: 100%;\r\n    top: 0;\r\n    z-index: 1000;\r\n\r\n    .brand {\r\n      font-size: 1.5rem;\r\n      \r\n      a {\r\n        color: #fff;\r\n        text-decoration: none;\r\n        padding: 1em;\r\n      }\r\n      \r\n    }\r\n\r\n    .menu-toggle {\r\n      display: none;\r\n      font-size: 1.5em;\r\n      cursor: pointer;\r\n    }\r\n   \r\n    /* Navigation Bar */\r\n  .navbar {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n\r\n    \r\n    .navbar-menu {\r\n      list-style-type: none;\r\n      display: flex;\r\n\r\n      li {\r\n        margin-right: 20px;\r\n\r\n        a {\r\n          text-decoration: none;\r\n          padding: 1em;\r\n          \r\n          .nav-icon{\r\n            display: none;\r\n          }\r\n\r\n          span{\r\n            color: #fff;\r\n          }\r\n        }\r\n        \r\n      }\r\n    }\r\n     \r\n\r\n    .navbar-menu li a:hover{\r\n      background-color: #FF5001;\r\n    }\r\n  }\r\n\r\n  }\r\n  \r\n  \r\n  // Hero Element\r\n  .hero {\r\n    position: relative;\r\n    display: flex;\r\n    align-items: center;\r\n    min-height: 500px;\r\n    width: 100%;\r\n    background-image: url('../public/images/heros/hero-image_2.jpg');\r\n    background-position: center;\r\n    background-size: cover;\r\n    padding: 0 20px;\r\n  \r\n    &::after {\r\n      content: '';\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n      background-color: rgba(0, 0, 0, 0.5);\r\n    }\r\n  \r\n    .hero-inner {\r\n      z-index: 10;\r\n      display: flex;\r\n      flex-direction: column;\r\n      justify-content: center;\r\n      margin: 70px 70px 70px 120px;\r\n      max-width: 800px;\r\n      padding-top: 70px;\r\n  \r\n      .hero-title {\r\n        color: #fff;\r\n        font-weight: 500;\r\n        font-size: 40px;\r\n      }\r\n      .hero-tagline {\r\n        color: #fff;\r\n        margin-top: 16px;\r\n        font-size: 18px;\r\n        font-weight: 300;\r\n        width: 70%;\r\n      }\r\n\r\n      .hero-button {\r\n        background-color: $primary-color;\r\n        color: #fff;\r\n        border: none;\r\n        padding: 0.9em;\r\n        margin-top: 20px;\r\n        font-size: 16px;\r\n        cursor: pointer;\r\n        width: 25%;\r\n        border-radius: 5px;\r\n\r\n        &:hover {\r\n          background-color: $accent-color;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  \r\n  // Content Heading\r\n  .head-content {\r\n    text-align: center;\r\n    margin-top: 38px;\r\n  \r\n    h1 {\r\n      font-size: 2rem;\r\n      color: $secondary-color;\r\n      margin-bottom: 10px;\r\n    }\r\n  \r\n    &::after {\r\n      content: '';\r\n      display: block;\r\n      width: 90px;\r\n      height: 2px;\r\n      background-color: $primary-color;\r\n      margin: 10px auto;\r\n    }\r\n  }\r\n  \r\n  // CSS untuk Form Pencarian\r\n  .search-container {\r\n    margin-top: 20px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n  \r\n    .search-form {\r\n      display: inline-block;\r\n  \r\n      input {\r\n        width: 600px;\r\n        padding: 1em;\r\n        font-size: 16px;\r\n        border: 1px solid #ccc;\r\n        border-radius: 4px;\r\n      }\r\n  \r\n      button {\r\n        padding: 1em;\r\n        font-size: 16px;\r\n        background-color: $primary-color;\r\n        color: #fff;\r\n        border: none;\r\n        border-radius: 4px;\r\n        cursor: pointer;\r\n  \r\n        &:hover {\r\n          background-color: $accent-color;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  \r\n  // Daftar Restoran\r\n  .container-restaurants {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin-top: 20px;\r\n    margin-bottom: 50px;\r\n    padding: 40px;\r\n  \r\n    .restaurant-list {\r\n      display: grid;\r\n      grid-template-columns: repeat(3, 1fr);\r\n      gap: 40px;\r\n      justify-content: space-evenly;\r\n  \r\n      .restaurant-item {\r\n        max-width: 350px;\r\n        border: 1px solid #ddd;\r\n        position: relative;\r\n        border-radius: 5px;\r\n        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);\r\n  \r\n        img {\r\n          width: 100%;\r\n          max-height: 170px;\r\n          display: block;\r\n        }\r\n  \r\n        .city-label {\r\n          position: absolute;\r\n          top: 25px;\r\n          background-color: $primary-color;\r\n          color: white;\r\n          padding: 5px 10px;\r\n          border-radius: 0px 5px 5px 0px;\r\n  \r\n          p {\r\n            font-weight: bold;\r\n          }\r\n        }\r\n  \r\n        .content-item {\r\n          margin-top: 10px;\r\n          padding: 20px;\r\n  \r\n          h2 {\r\n            font-size: 1.2em;\r\n            margin-bottom: 5px;\r\n          }\r\n  \r\n          p {\r\n            font-size: 0.9em;\r\n            margin-bottom: 5px;\r\n          }\r\n        }\r\n  \r\n        .rating {\r\n          margin-top: 10px;\r\n          margin-bottom: 5px;\r\n          display: flex;\r\n          align-items: center;\r\n          font-size: 1em;\r\n  \r\n          i {\r\n            color: yellow;\r\n            margin-right: 5px;\r\n            font-size: 20px;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n  \r\n  // Footer\r\n  footer {\r\n    background-color: $secondary-color;\r\n    color: #fff;\r\n    text-align: center;\r\n    padding: 20px;\r\n  }\r\n  "],"sourceRoot":""}]);
